export function formatAddress(address) {
	const reg = /.+?(|||||)/g; //
	let region = address.match(reg); ////
	let newAddress;
	if (region.length > 2) { //
		let str = region.reduce((pre, item, index) => {
			if (index < 3) {
				pre.push(item) //
			}
			return pre;
		}, [])

		region = str;
		// console.log(`str:${str}`)
		// region = str;   //region
		newAddress = address.replace(str.join(''), ''); //
	} else {
		console.log([...region])
		let str = [...region]; //regionstr
		str = str.join(''); //
		newAddress = address.replace(str, ''); //
	}
	return [region, newAddress]
}

export const calculation = (total, couponAmount) => { //,,
	let baseNum, baseNum1, baseNum2;
	let precision; // 
	try {
		baseNum1 = total.toString().split(".")[1].length;
	} catch (e) {
		baseNum1 = 0;
	}
	try {
		baseNum2 = couponAmount.toString().split(".")[1].length;
	} catch (e) {
		baseNum2 = 0;
	}
	baseNum = Math.pow(10, Math.max(baseNum1, baseNum2));
	precision = (baseNum1 >= baseNum2) ? baseNum1 : baseNum2;
	return ((total * baseNum - couponAmount * baseNum) / baseNum).toFixed(precision);
}

export function formatDate(ary) {
	let str = /\//g; //
	let result = ary.map(item => {
		item.updateTime = item.updateTime.replace(str, "-")
		return item
	})
	return result
}
