import {
	request,
	bannerTypeId,
	bannerId
} from './request.js';

// =>
export function getIndexRecommend() {
	return request({
		opt: 'getBanner',
		typeId: bannerTypeId
	})
}


// =>
export function getIndexBanner() {
	return request({
		opt: 'getBanner',
		typeId: bannerId
	})
}

// => 
export function getIndexSlideNav() {
	return request({
		opt: 'getType',
		parentId: 0
	})
}
// => 
export function getIndexType() {
	return request({
		opt: 'getIndexType',
		parentId: 0
	})
}

// => 
// type:
export function getIndexYouLike(type){
	return request({
					opt: 'getLikeProduct',
					openid: '',
		      type: type
				})
}