import {
	request
} from './request.js';

// Vip
export function isVip(userId) {
	return request({
		opt: 'getVip',
		user_id: userId,
	})
}

// Vip
export function vipList() {
	return request({
		opt: 'getVipList',
		strWhere: 'type=1 or type=0'
	})
}

// 
export function vipUser(userId) {
	return request({
	  opt: 'getUser',
	  userId: userId
	})
}
